.sidebar {
    display: flex;
    flex-direction: column;
    flex: 0.35;
}


.sidebar_header {
    display:flex;
    justify-content: space-between;
    padding: 20px;
    border-right: 1px solid lightgray;
}

.sidebar_header_right {
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 10vh;
} 

.sidebar_header_right > .MuiSvgIcon-root {
    margin-right: 2vw;
    font-size: 24px;
}

.sidebar_search {
    display: flex;
    align-items: center;
    background-color: #f6f6f6;
    height: 40px;
    padding: 10px;

}

.sidebar_searchContainer {
    display: flex;
    align-items: center;
    background-color: white;
    width: 100%;
    height: 35px;
    border-radius: 20px;
}

.sidebar_searchContainer > .MuiSvgIcon-root {
    color: grey;
    padding: 10px;
}

.sidebar_searchContainer > input {
    border: none;
    outline-width: 0;
    margin-left: 10px;
}



.sidebar_chats {
    flex: 1;
    background-color: white;
    overflow: scroll;
}