.chat {
    display: flex;
    flex-direction: column;
    flex: 0.65;
}

.chat_header {
    padding: 20px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid lightgray; 
}

.chat_headerInfo {
    flex: 1;
    padding-left: 20px;
}

.chat_headerInfo > h3 {
    margin-bottom: 3px;
    font-weight: 500;
}

.chat_headerInfo > p {
    color: gray;
}

.chat_body {
    flex: 1;
    background-image: url("https://i.imgur.com/E6akUb3.jpg");
    background-repeat: no-repeat;
    background-position: center;
    padding: 30px;
    overflow: scroll;
}

.chat_message {
    position: relative;
    font-size: 18px;
    padding: 10px;
    width: fit-content;
    border-radius: 10px;
    background-color: #fffee4;
    margin-bottom: 30px;
}

.chat_timestamp {
    margin-left: 10px;
    font-size: xx-small;
}

.chat_name {
    color: white;
    position: absolute;
    top: -15px;
    font-weight: 700;
    font-size: x-small;
    font-family: Helvetica;
}

.chat_reciever {
    margin-left: auto;
    background-color: #78C5EF;
}

.chat_footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 65px;
    border-top: 1px solid lightgrey;
}

.chat_footer > form {
    flex: 1;
    display: flex;
}

.chat_footer > form > input {
    flex: 1;
    border-radius: 30px;
    padding: 10px;
    border: none;    
    outline-width: 0;

}

.chat_footer > form > button {
    display: none;
}

.chat_footer > .MuiSvgIcon-root  {
    padding: 10px;
    color: gray;
}